<!--公用组件：
 /**
  * @desc EchartsRoseType
  * @module @/components
  * @desc 组件描述：极限螺旋图
  * @author zkhq
  * @date 2020/3/13 10:48
  * @param {Object} [title]    - 参数说明
  * @param {String} [columns] - 参数说明
  * @example 调用示例
  *  <hbTable :title="title" :columns="columns" :tableData="tableData"></hbTable>
  */
-->
<template>
  <div
    class="full-all"
    ref="roseType"
  ></div>
</template>

<script>
// import
export default {
  name: 'EchartsRoseType',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      // echarts 对象
      ec: {},
      options: {
        title: {
          text: '南丁格尔玫瑰图',
          subtext: '纯属虚构',
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          left: 'center',
          top: 'bottom',
          data: ['rose1', 'rose2', 'rose3', 'rose4', 'rose5', 'rose6', 'rose7', 'rose8']
        },
        toolbox: {
          show: true,
          feature: {
            mark: {show: true},
            dataView: {show: true, readOnly: false},
            magicType: {
              show: true,
              type: ['pie', 'funnel']
            },
            restore: {show: true},
            saveAsImage: {show: true}
          }
        },
        series: [
          {
            name: '面积模式',
            type: 'pie',
            radius: [30, 110],
            roseType: 'area',
            label: {
              position: 'outside',
              rotate: true,
              distanceToLabelLine: 0
            },
            labelLine: {
              show: false,
              length: 0,
              length2: 0
            },
            data: [
              {value: 10, name: 'rose1', labelLine: {}},
              {value: 5, name: 'rose2'},
              {value: 15, name: 'rose3'},
              {value: 25, name: 'rose4'},
              {value: 20, name: 'rose5'},
              {value: 35, name: 'rose6'},
              {value: 30, name: 'rose7'},
              {value: 40, name: 'rose8'}
            ]
          }
        ]
      }
    }
  },
  props: {
    // id: {
    //   default: '',
    //   type: String
    // }
  },
  components: {},
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
    let data = this.options.series[0].data
    let arr = data.map(obj => obj.value)
    let max = Math.max.apply({}, arr)
    for (let i = 0, l = data.length; i < l; i++) {
      let obj = data[i]
      obj.labelLine = {length: (obj.value - max) * 80 / max + 10}
    }
    // 生成 echarts 对象
    this.ec = this.$echarts.init(this.$refs.roseType)
    // 设置 echarts 对象的配置项，使其生成扇形图
    this.ec.setOption(this.$common.deepClone(this.options))
    // 监听 window 的 resize 事件，调整扇形图的大小
    this.$common.windowResize(this.resize)
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
    // 删除事件监听，减少资源的消耗
    this.$common.removeWindowResize(this.resize)
  },
  destroyed () {
  },
  watch: {},
  computed: {},
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
    /**
     * @desc 使 echarts 扇形图根据当前的容器自适应（用于添加到 window 自适应事件中）
     */
    resize () {
      // 如果没有 resize 方法，可能会报错
      if (this.$common.objC(this.ec, 'resize')) {
        this.ec.resize()
      }
    }
  }
}
</script>

<style scoped>

</style>



